.movieInfo {
  width: 100%;
  position: relative;
  background-size: cover;
  min-height: 815px;
}
.movieContent {
  position: relative;
  z-index: 4;
  width: 500px;
  left: 10vw;
  top: 5vw;
}
.poster {
  max-width: 400px;
  display: block;
  position: relative;
  left: 100px;
  border-radius: 5px;
}

.movieText {
  width: 100%;
  position: absolute;
  top: 100px;
  left: 700px;
  color: #fff;
  opacity: 1;
  font-size: 10px;

}
